* {
  box-sizing: border-box;
}

body {
  text-align: center;
  color: #2f4f4f;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  transition: border-radius 1s;
  display: flex;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  transition: transform 1s;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.person:hover img {
  border-radius: 60px;
}

.person:hover .person__extra {
  display: block;
}

.person__photo {
  position: relative;
}

.person__rank {
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 32px;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  margin-top: 8px;
}

.person__extra {
  color: #000;
  margin-top: 9px;
  margin-bottom: 19px;
  font-size: 18px;
  line-height: 20.7px;
  display: none;
}
